/*
======================
Footer
======================
*/

.footer {
  background-color: $black3;
  padding: 6rem 1rem;
  line-height: 3rem;
}

.footer-top__box span svg {
  width: 1.6rem;
  height: 1.6rem;
  fill: $grey3;
}

.footer-top__box span {
  margin-right: 1rem;
}

.footer__top {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  color: $grey3;
}

.footer-top__box a:link,
.footer-top__box a:visited {
  display: block;
  color: $grey;
  font-size: 1.4rem;
  transition: 0.6s;
}

.footer-top__box a:hover {
  color: $orange;
}

.footer-top__box div {
  color: $grey;
  font-size: 1.4rem;
}

.footer-top__box h3 {
  font-size: 1.8rem;
  font-weight: 400;
  margin-bottom: 1rem;
}

@media only screen and (max-width: 998px) {
  .footer__top {
    grid-template-columns: repeat(2, 1fr);
    row-gap: 2rem;
  }
}

@media only screen and (max-width: 768px) {
  .footer__top {
    grid-template-columns: 1fr;
    row-gap: 2rem;
  }
}
