  align-items: center;
  justify-content: center;
  position: absolute;
  top: 2rem;
  right: 2rem;
  padding: 1.5rem;
  background-color: #f1f1f1;
}

.popup__close {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 2rem;
  right: 2rem;
  padding: 1.5rem;
  border-radius: 50%;
  cursor: pointer;
}

.popup__close svg {
  width: 1.7rem;
  height: 1.7rem;
}

.popup__left {
  display: table-cell;
  width: 50%;
  height: 100%;
  vertical-align: middle;
}

.popup__right {
  display: table-cell;
  width: 50%;
  vertical-align: middle;
  padding: 3rem 5rem;
}

.popup-img__container {
  width: 100%;
  overflow: hidden;
}

.popup-img__container img.popup__img {
  display: block;
  width: 60rem;
  height: 45rem;
  max-width: 100%;
  border-radius: 1rem;
  object-fit: cover;
}

.popup .right__content {
  text-align: center;
  width: 85%;
  margin: 0 auto;
}

.popup .right__content h1 {
  font-size: 4rem;
  color: #222;
  margin-bottom: 1.6rem;
}

.popup .right__content h1 span {
  color: #59b210;
}

.popup .right__content p {
  font-size: 1.8rem;
  color: #666;
  margin-bottom: 1.6rem;
}

.popup .right__content a:link,
.popup .right__content a:visited {
  display: inline-block;
  padding: 1.8rem 5rem;
  border-radius: 3rem;
  font-weight: 700;
  color: #fff;
  background-color: #222;
  border: 1px solid #666;
  transition: 0.3s;
}

.popup .right__content a:hover {
  background-color: #59b210;
  border: 1px solid #666;
  color: #222;
}

.popup__form {
  width: 100%;
  padding: 2rem 0;
  text-indent: 1rem;
  margin-bottom: 1.6rem;
  border-radius: 3rem;
  background-color: #f1f1f1;
  border: none;
}

.popup__form:focus {
  outline: none;
}

.hide__popup {
  transform: scale(0.2);
  opacity: 0;
  visibility: hidden;
}

@media only screen and (max-width: 1200px) {
  .right__content {
    width: 100%;
  }
  .right h1 {
    font-size: 3.5rem;
    margin-bottom: 1.3rem;
  }
}

@media only screen and (max-width: 999px) {
  .popup__right {
    width: 100%;
  }
  .popup__left {
    display: none;
  }
  .popup .right__content h1 {
    font-size: 5rem;
  }
}

@media only screen and (max-width: 768px) {
  .right__content h1 {
    font-size: 4rem;
  }
  .right__content p {
    font-size: 1.6 rem;
  }
  .right__content__form {
    width: 90%;
    margin: 0 auto;
    padding: 1.8rem 0;
    margin-bottom: 1.5rem;
  }
}

@media only screen and (max-width: 568px) {
  .popup__right {
    padding: 0 1.6rem;
  }
  .popup__content {
    height: 35rem;
    width: 90%;
    margin: 0 auto;
  }
  .popup__close {
    top: 1rem;
    right: 1rem;
    padding: 1.3rem;
  }
  .popup__close svg {
    width: 1.4rem;
    height: 1.4rem;
  }
  .popup form {
    width: 100%;
    padding: 1.5rem 0;
    margin-bottom: 1.3rem;
  }
  .popup a:link,
  .popup a:visited {
    padding: 1.5rem 3rem;
  }
  __right-content {
    width: 100%;
  }
  __right-content h1 {
    font-size: 3rem;
  }
  __right-content p {
    font-size: 1.4rem;
  }
}

.goto-top:link, .goto-top:visited {
  position: fixed;
  right: 2%;
  bottom: 10%;
  padding: 0.8rem 1.4rem;
  border-radius: 1rem;
  background-color: #eb0028;
  visibility: hidden;
  cursor: pointer;
  transition: 0.3s;
  animation: bounce 2s ease-in-out inifinite;
}

.goto-top svg {
  width: 1.3rem;
  height: 1.3rem;
  fill: #fff;
}

.goto-top:hover {
  background-color: #000;
}

.show-top:link, .show-top:visited {
  visibility: visible;
  z-index: 1999;
}

@keyframes bounce {
  0% {
    transform: scale(0.5);
  }
  50% {
    transform: scale(1.5);
  }
  0% {
    transform: scale(1);
  }
}

@media only screen and (max-width: 768px) {
  .goto-top:link, .goto-top:visited {
    right: 5%;
    bottom: 5%;
  }
}

.facility__section {
  background-color: #f1f1f1;
  padding: 6rem 0;
}

.facility__container {
  display: grid;
  align-items: center;
  grid-template-columns: repeat(4, 1fr);
}

.facility-img__container {
  position: relative;
  display: inline-block;
  line-height: 9.5rem;
  width: 8rem;
  height: 8rem;
  border-radius: 50%;
  border: 1.5px solid #fff;
  z-index: 1;
  margin-bottom: 1.6rem;
}

.facility-img__container svg {
  width: 3rem;
  height: 3rem;
  transition: 1s;
  perspective: 4000;
}

.facility-img__container:before {
  content: "";
  position: absolute;
  display: inline-block;
  border-radius: 50%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0.7rem;
  background-color: #fff;
  z-index: -1;
}

.facility__box {
  text-align: center;
}

.facility__box:hover svg {
  transform: rotateY(180deg);
  line-height: 9.5rem;
}

@media only screen and (max-width: 998px) {
  .facility__container {
    grid-template-columns: 1fr 1fr;
    row-gap: 3rem;
  }
}

@media only screen and (max-width: 568px) {
  .facility__container {
    grid-template-columns: 1fr;
  }
  .facility__container p {
    font-size: 1.4rem;
  }
  .facility-img__container {
    width: 7rem;
    height: 7rem;
    line-height: 8.5rem;
  }
}

